.configHeader {
  margin: 3rem 0rem;
  max-width: 1150px;
}
.configHeader p {
  opacity: 1;
}

.configHeader .configHead {
  font-size: 3rem;
  font-weight: 800;
  opacity: 0.8;
  text-align: center;
}

.configHeader :not(.configHead) {
  line-height: 2rem;
  font-weight: 600;
  opacity: 0.7;
}
